@charset "UTF-8";

.bible_bg {
  width: 100%;
  height: 100%;
  background-color: rgb(250, 247, 52);
  background: url("../img/bible/clouds.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  animation: moveBackground 60s linear infinite;
  /* Safari and Chrome: */
  -webkit-animation: moveBackground 60s linear infinite;
}
@keyframes moveBackground {
  from {
    background-position: 0 100%;
  }
  to {
    background-position: 100% 100%;
  }
}
@-webkit-keyframes moveBackground {
  from {
    background-position: 0 100%;
  }
  to {
    background-position: 100% 100%;
  }
}

.bible-menu {
  position: fixed;
  top: 20px;
  left: 20px;
  color: #fff;
  font-family: "Helvetica";
  display: flex;
  flex-direction: column;
}

.bible_text {
  font-family: "Helvetica";
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  justify-content: center;
  text-align: center;
  color: #fff;
  text-shadow: 5px 5px #0053ff;
  font-size: 4vw;
}

.bible_text p:hover {
  color: #fff;
  text-shadow: 0 0 5px #fff, 0 0 10px #fff, 0 0 20px #fff,
    0 0 40px rgb(255, 196, 0), 0 0 80px rgb(255, 217, 0),
    0 0 90px rgb(229, 255, 0), 0 0 150px rgb(245, 248, 42);
}
